<template>
  <div id="page-user-view">

    <vs-alert color="danger" :title="$t('book.notFound.title')" :active.sync="book_not_found">
      <span>{{ $t('book.notFound.message', { bookId: $route.params.bookId }) }} </span>
    </vs-alert>

    <div id="user-data" v-if="bookData">

      <vx-card :title="$t('book.book')" class="mb-base">

        <div class="vx-row">

          <!-- Information - Col 1 -->
          <div class="vx-col flex-1" id="account-info-col-1">
            <table>
              <tr>
                <td class="font-semibold">{{ this.$t('book.title') }}</td>
                <td>{{ bookData.title }}</td>
              </tr>
              <tr>
                <td class="font-semibold">{{ this.$t('book.author') }}</td>
                <td>{{ bookData.author }}</td>
              </tr>
              <tr>
                <td class="font-semibold">{{ this.$t('book.publishingHouse') }}</td>
                <td>{{ bookData.publishingHouse }}</td>
              </tr>
            </table>
          </div>
          <!-- /Information - Col 1 -->

          <!-- Information - Col 2 -->
          <div class="vx-col flex-1" id="account-info-col-2">
            <table>
              <tr>
                <td class="font-semibold">{{ this.$t('book.isbn') }}</td>
                <td>{{ bookData.isbn }}</td>
              </tr>
              <tr>
                <td class="font-semibold">{{ this.$t('book.price') }}</td>
                <td>{{ bookData.price.toLocaleString('pt-BR', { style: 'currency', currency: 'BRL' }) }}</td>
              </tr>
              <tr>
                <td class="font-semibold">{{ this.$t('book.status') }}</td>
                <td>{{ bookData.status }}</td>
              </tr>
            </table>
          </div>
          <!-- /Information - Col 2 -->
          <div class="vx-col w-full flex" id="account-manage-buttons">
            <vs-button icon-pack="feather" icon="icon-edit" class="mr-4" :to="{name: 'admin-book-edit', params: { bookId: $route.params.bookId }}">{{ $t('button.edit') }}</vs-button>
          </div>

        </div>

      </vx-card>
    </div>
  </div>
</template>

<script>
import BookManagement from '@/store/book-management/moduleBookManagement.js'

export default {
  data() {
    return {
      bookData: null,
      book_not_found: false,
    }
  },
  methods: {
    confirmDeleteRecord() {
      this.$vs.dialog({
        type: 'confirm',
        color: 'danger',
        title: `Confirm Delete`,
        text: `You are about to delete "${this.bookData.username}"`,
        accept: this.deleteRecord,
        acceptText: "Delete"
      })
    },
    deleteRecord() {
      /* Below two lines are just for demo purpose */
      this.$router.push({name:'app-user-list'});
      this.showDeleteSuccess()

      /* UnComment below lines for enabling true flow if deleting user */
      // this.$store.dispatch("bookManagement/removeRecord", this.bookData.id)
      //   .then(()   => { this.$router.push({name:'app-user-list'}); this.showDeleteSuccess() })
      //   .catch(err => { console.error(err)       })
    },
    showDeleteSuccess() {
      this.$vs.notify({
        color: 'success',
        title: 'User Deleted',
        text: 'The selected user was successfully deleted'
      })
    }
  },
  created() {
    // Register Module UserManagement Module
    if(!BookManagement.isRegistered) {
      this.$store.registerModule('bookManagement', BookManagement)
      BookManagement.isRegistered = true
    }

    const bookId = this.$route.params.bookId
    this.$store.dispatch("bookManagement/fetchBook", bookId)
      .then(res => { this.bookData = res.data.attributes })
      .catch(err => {
        if(err.response.status === 404) {
          this.book_not_found = true
          return
        }
        console.error(err) })
  }
}

</script>

